.header {
  background-color: #735c44;
}

.navbar {
  color: #FFFFFF;
}

.navbar__logo {
  color: #C0AD95;
}

.main-menu__link:hover {
  background-color: rgba(255, 255, 255, 0.15);
}

.banner {
  color: #FFFFFF;
}

.main-socials::after,
.main-socials::before {
  background: #FFFFFF;
}

.form-button {
  color: #FFFFFF;
  background-color: #AF9678;
}

.form-input {
  color: #11100E;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  height: 100%;
}

body {
  display: flex;
  flex-direction: column;
  height: 100%;
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-family: "Gilroy", "Futura", sans-serif;
  font-style: normal;
  font-size: 1rem;
  line-height: 1.25;
  font-weight: normal;
  text-transform: unset;
  color: #000000;
  background-color: #FFFFFF;
}

main {
  flex-grow: 1;
}

img {
  display: inline-block;
  max-width: 100%;
  height: auto;
}

a {
  display: inline-block;
  text-decoration: none;
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
  color: inherit;
}

.list-default {
  list-style: none;
  margin: 0;
  padding: 0;
}

.container-fluid {
  width: 100%;
}

@media (max-width: 1169.9px) {
  .container-fluid {
    padding: 0 15px;
  }
}
@media (min-width: 1170px) and (max-width: 1439.9px) {
  .container-fluid {
    padding: 0 130px;
  }
}
@media (min-width: 1440px) {
  .container-fluid {
    padding: 0 130px;
  }
}
.hidden {
  display: none;
}

.hidden__no {
  display: inline-block;
}

@media (max-width: 1169.9px) {
  .hidden_mobile {
    display: none;
  }
}
@media (min-width: 1170px) {
  .hidden_desktop {
    display: none;
  }
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
  white-space: nowrap;
  clip-path: inset(100%);
}

.header {
  min-height: 100vh;
  height: 100vh;
}

.header__navbar {
  margin-top: 30px;
}

.navbar {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  flex-wrap: wrap;
  font-family: "Gilroy", "Futura", sans-serif;
  font-style: normal;
  font-size: 24px;
  line-height: 1.167;
  font-weight: normal;
  text-transform: unset;
  min-height: 60px;
  height: 60px;
}

.navbar__logo {
  font-family: "PlayfairDisplay", "Geneva", sans-serif;
  font-style: normal;
  font-size: 32px;
  line-height: 1.625;
  font-weight: normal;
  text-transform: uppercase;
}

.main-menu {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.main-menu__item {
  display: flex;
  align-items: stretch;
  margin-right: 20px;
  margin-bottom: 2px;
}

.main-menu__item:last-child {
  margin-right: 0;
}

.main-menu__link {
  display: flex;
  align-items: center;
  max-width: 140px;
  padding-left: 5px;
  padding-right: 5px;
}

.fast-contacts__phone {
  text-align: center;
}

.fast-contacts__schedule {
  text-align: center;
}

.banner {
  width: 43%;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  flex-wrap: nowrap;
  font-family: "Gilroy", "Futura", sans-serif;
  font-style: normal;
  font-size: 26px;
  line-height: 28px;
  font-weight: normal;
  text-transform: unset;
  padding-top: 170px;
}

.banner__title {
  margin-bottom: 40px;
  font-family: "Cormorant", "Futura", sans-serif;
  font-style: normal;
  font-size: 70px;
  line-height: 1.25;
  font-weight: normal;
  text-transform: unset;
}

.banner__description {
  margin-bottom: 20px;
  font-size: 33px;
}

.banner__counter {
  margin-bottom: 30px;
}

.banner__input {
  margin-bottom: 30px;
}

.counter__item-label {
  font-size: 21px;
}

.counter__title {
  margin-bottom: 9px;
}

.counter__items {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.counter__item {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  flex-direction: column-reverse;
  align-items: center;
  margin-right: 13px;
}

.main-area {
  position: relative;
  height: calc(100vh - 60px - 30px);
}

.main-area__socials {
  position: absolute;
  top: 0;
  right: 0;
}

.main-socials {
  width: 26px;
  height: 100%;
}

.main-socials::before {
  content: "";
  position: absolute;
  top: 0;
  right: 10px;
  width: 2px;
  height: calc(50% - 57px - 5px);
}

.main-socials::after {
  content: "";
  position: absolute;
  bottom: 0;
  right: 10px;
  width: 2px;
  height: calc(50% - 57px - 5px);
}

.main-socials__list {
  list-style: none;
  padding: 0;
  margin: 0;
  min-height: 114px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  flex-wrap: wrap;
  position: absolute;
  top: calc(50% - 57px);
  right: 0;
}

.form-input {
  display: inline-block;
  padding: 11px 20px;
  min-width: 240px;
  font: inherit;
  font-family: "Gilroy", "Futura", sans-serif;
  font-style: normal;
  font-size: 26px;
  line-height: 1.077;
  font-weight: normal;
  text-transform: unset;
  vertical-align: middle;
  border: none;
  border-radius: 8px;
}
.form-input--mini {
  padding: 6px 8px;
  min-width: 44px;
  text-align: center;
  border-radius: 0;
}

.form-button {
  align-self: flex-start;
  display: inline-block;
  padding: 14px 28px;
  min-width: 240px;
  font: inherit;
  font-family: "Gilroy", "Futura", sans-serif;
  font-style: normal;
  font-size: 26px;
  line-height: 1.077;
  font-weight: normal;
  text-transform: unset;
  text-align: center;
  vertical-align: middle;
  border: none;
  border-radius: 8px;
}